reen and (max-width: 640px){.hero-cta-content .part-1 div:nth-child(2) h1{transform:translate(5rem, -5rem)}}.hero-cta-content .part-1 div:nth-child(3){display:flex;justify-content:flex-start;align-items:center;transform:translateY(-500%)}.hero-cta-content .part-1 div:nth-child(3) button{margin:0 3vw}.hero-cta-content .part-1 div:nth-child(3) .btn-cta{opacity:1}@media screen and (max-width: 640px){.hero-cta-content .part-1 div:nth-child(3){transform:translateY(-100%);display:flex;flex-direction:column;justify-content:center;align-items:center}}.fun-fact{position:relative}.fun-fact div{display:flex}.fun-fact div div{display:flex;flex-direction:column;flex:2 1;margin:0 5vw}.fun-fact div div h3,.fun-fact div div p{-webkit-clip-path:polygon(0 100%, 100% 100%, 100% 100%, 0 100%);clip-path:polygon(0 100%, 100% 100%, 100% 100%, 0 100%)}.fun-fact div span{position:relative;display:flex;opacity:1;-webkit-clip-path:circle(0% at 50% 50%);clip-path:circle(0% at 50% 50%);width:50%;transform:scale(3)}.fun-fact div span img{position:absolute;transform:translateX(15%)}@media screen and (max-width: 640px){.fun-fact div span{position:absolute;height:50%;aspect-ratio:1}.fun-fact div span:nth-child(1){top:-40%;left:25%}.fun-fact div span:nth-child(2){top:95%;left:25%}.fun-fact div span img{transform:translateX(10%) scale(0.9)}}.showCar{display:inline-block}.showAndre,.showFlaminio{display:none}
